<template>
    <div class="content">
        <template v-if="playPl2">
        <div class="fl-layout">
            <div class="wrap" :style="{'transform': `translateY(${left==0 || left == 1 || left == 2 ? 0 : left < (playPl2.length-2) ? -(left-2)*0.66 : -(playPl2.length-5) * 0.66}rem)`}">
                <div @click="toggleType(index)" :class="['item',{'active': index == left}]" v-for="(item, index) in playPl2" :key="index">
                    <span><var>{{item.name}}</var></span>
                </div>
            </div>
        </div>
        <div class="fr-layout">
            <van-swipe ref="swipe" class="one" vertical @change="onChange" :show-indicators="false" :loop="false" :initial-swipe="init">
                <van-swipe-item class="ul" v-for="(item, index) in playPl2" :key="index">
                    <li @click="selectTouzhu" v-for="(item1, index1) in item.sscPlayPlList" :key="index1">
                        <strong>{{item1.name}}</strong>
                        <span>{{item1.playPl}}</span>
                        <input hidden class="name" :value="item['name'] + '-' + item1.name" type="text">
                        <input hidden class="playPl" :value="item1.playPl" type="text">
                        <input hidden class="playPlId" :value="item1.playPlId" type="text">
                    </li>
                </van-swipe-item>
            </van-swipe>
        </div>
        </template>
        <loadding v-else></loadding>
    </div>
    
</template>

<script>
    import { mapState } from "vuex";
    import { 
        GAME_GET_SSC_PLAY_PL2
    } from "@/tool/common/constants.js";

    import $ from "jquery";

    export default {
        name: 'Play1',
        props:['playId'],
        data(){
            return {
                left: 0,
                init:0,
                playPl2: false,
            }
        },
        created(){
            this.getSscPlayPl2()
        },
        computed: {
            ...mapState('Game', {
                playGroupId: state => state.playGroupId,
                number: state => state.getSscOpenTime2.number,
                chip: state => state.chip,
            })
        },
        mounted(){
            
        },
        methods: {
            // 滑动切换
            onChange(index) {
                this.left = index
                setTimeout(() => {
                    this.init = index
                }, 500);
            },
            // 切换类型
            toggleType(index){
                this.$refs.swipe.swipeTo(index)
                this.left = index
            },
            // 选择投注码
            selectTouzhu(e){
                var $even = e.target.parentNode.classList
                if($even.contains('active')){
                     $even.remove('active')
                } else{
                    $even.add('active')
                }
                this.sscBetList()
            },
            // 所选注数
            sscBetList(){
                var _this = this
                var sscBetList = []
                $('.content .fr-layout .ul li.active').each(function(){
                    var playPlId = $(this).find(".playPlId").val()
                    var playPl = $(this).find(".playPl").val()
                    var name = $(this).find(".name").val()
                    console.log(playPlId);
                    console.log(playPl);
                    console.log(name);
                    var zhushu = 1
                    var zhu = {
                        "number": _this.number,
                        "playGroupId": _this.playGroupId,
                        "beishu":1,
                        "zhushu":zhushu,
                        "content": name,	
                        "playId": _this.playId,
                        "playPl":playPl,
                        "playPlId":playPlId,
                        "totalMoney":_this.chip,
                        "perMoney":_this.chip * zhushu,
                    }
                    sscBetList.push(zhu)
                })
                this.$store.commit('Game/SET_SSC_BET_LIST', sscBetList)
            },
            
            // 当前玩法赔率
            getSscPlayPl2(){
                var data = {
                    playId: this.playId
                }
                // this.$store.dispatch(`Game/${GAME_GET_SSC_PLAY_PL2}`)
                this.$api.game[GAME_GET_SSC_PLAY_PL2](data)
                .then(res=>{
                    var list =  res.sscPlayPlGroupList
                    list.pop()
                    this.playPl2 = list
                })
            },
        }
    }
</script>

